




.sticky{position:sticky;top:0;background:white;}
.nav{
  display:inline;
  margin-left: 10px;
  color:black;
  
}
.nav:hover{background:grey;color:white;}
#links{

 
  list-style-type: none;
 
  padding: 0;
  width: 90vw;
  background-color:lightskyblue;

}
li{
  display: block;
  color: #000;
  padding: 8px 16px;
 
  color:black;
  border:1px solid black;
 

}
li:hover{
  background: grey;
  color:white;

}
Link{
  text-decoration: none;
}
#margin{
  display:grid;
  grid-template-columns:33% auto;
}
